body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --mobile-links-color: #111623;
  --logo-color: #1b253b;
  --header-links-color: #bb99f4;
  --border-color: rgba(168, 198, 243, 0.7);
  --design-color: linear-gradient(
    100.2deg,
    rgba(231, 189, 251, 0.8) -10.5%,
    rgba(250, 208, 218, 0.6) 65%,
    rgba(168, 198, 243, 0.7) 95%
  );
  --text-color: rgba(35, 39, 42, 1);
  --project-color: rgba(129, 124, 125, 1);
  --linked-color: linear-gradient(
    to left top,
    #006699,
    #346c96,
    #4c7394,
    #607a91,
    #71808e,
    #717b85,
    #70767d,
    #6f7174,
    #5f6164,
    #4f5153,
    #404244,
    #313335
  );
  --facebook-color: rgb(41, 72, 125);
  --gmail-color: #ea4335;
  --whatsapp-color: #25d366;
  --linkedin-color: #2867b2;
  --slack-color: #4a154b;
  --discord-color: #7289da;
  --todo-color: #fbbc05;
  --weatherapp-color: #86888a;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  display: flex;
  position: sticky;
  top: 0;
  align-items: center;
  justify-content: space-between;
  background: whitesmoke;
  height: 60px;
  width: 100%;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.logo {
  color: var(--logo-color);
  font-size: 30px;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: bold;
  font-weight: 900;
  font-family: "Mulish", "sans-serif";
  padding-left: 30px;
}
.header-right {
  display: flex;
  align-items: center;
  flex: 0.6 1;
}
.nav {
  font-weight: 500;
  text-decoration: none;

  display: flex;
}
.links {
  display: flex;
  padding-left: 15px;
  font-family: "Lato", "sans-serif";
  color: var(--mobile-links-color);
  cursor: pointer;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  font-style: italic;
  text-transform: uppercase;
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
.links:hover {
  color: var(--mobile-links-color);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.active {
  color: var(--header-links-color);
}
.icon {
  color: var(--header-links-color);
}
.btn-sidebar {
  border: none;
  outline: none;
  cursor: pointer;
  padding-left: 15px;
  display: none;
  background-color: transparent;
}
@media (max-width: 992px) {
  .btn-sidebar {
    cursor: pointer;
    position: absolute;
    top: 5px;
    right: 5px;
    display: block;
  }
  .links {
    display: none;
  }
}
.mobile-sidebar {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: fixed;
  width: 300px;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  right: 0;
  transition: 2s all ease-in-out;
  top: 0;
  opacity: 1;
  z-index: 999;
  display: none;
  background: whitesmoke;
  box-shadow: 6px 2px 5px 8px rgba(0, 0, 0, 0.5);
}
.mobile-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile-closed {
  display: none;
}

.links-mobile {
  color: var(--project-color);
  padding-top: 45px;
  font-family: "Lato, Mulish, sans-serif";
  cursor: pointer;
  font-size: 18px;
  text-transform: capitalize;
  opacity: 0.8;
  transition: 0.3s ease-in;
}
.links-mobile:hover {
  color: var(--header-links-color);
  opacity: 1;
}

.avatar {
  margin-top: 15px;
  border: 2px solid var(--logo-color);
  cursor: pointer;
}

.close-mobile {
  position: absolute;
  left: 5px;
  top: 5px;
  outline: none;
  border: none;
  background-color: transparent;
}
.close-btn {
  color: var(--mobile-links-color);
  -webkit-animation: spin 5s infinite linear;
          animation: spin 5s infinite linear;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@media (max-width: 992px) {
  .mobile-sidebar {
    display: block;
  }
}
@media (max-width: 320px) {
  .mobile-sidebar {
    width: 320px;
  }
}
@media (max-width: 280px) {
  .mobile-sidebar {
    width: 280px;
  }
}

.hero {
  background: whitesmoke;
  height: 100vh;
  width: 100%;
  display: flex;
  z-index: -1;
  justify-content: space-evenly;
}
.hero-banner {
  background-color: var(--design-color);
  margin-top: 85px;
  padding-top: 15vh;
  width: 450px;
  height: 475px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  border: 2px solid var(--border-color);
  border-left-color: var(--header-links-color);
  border-bottom-color: var(--header-links-color);
  border-bottom-left-radius: 15px;
  border-top-right-radius: 15px;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: -5px -6px 6px 5px rgba(0, 0, 0, 0.3);
}
.hero-banner > h1 {
  padding-bottom: 15px;
  font-size: 42px;
  color: var(--text-color);
  font-weight: 900;
  font-family: "Mulish", "sans-serif";
  line-height: 1.1;
  letter-spacing: 0.8px;
}
.hero-banner > h4 {
  font-family: "Lato", "sans-serif";
  color: var(--project-color);
  font-weight: 400;
  font-style: italic;
  font-size: 18px;
}
.hero-banner > h4 > span {
  color: var(--text-color);
  font-family: "Mulish", "sans-serif";
  font-size: 20px;
  font-weight: 900;
}
.hero-right {
  display: flex;
  flex-direction: column;
  width: 350px;
  margin-top: 85px;
  height: 375px;
  padding-top: 10vh;
}
.hero-info > h3 {
  font-family: "Mulish", "sans-serif";
  font-size: 28px;
  padding-bottom: 10px;
  font-weight: 900;
  line-height: 1.1;
  letter-spacing: 0.8px;
}
.myData {
  cursor: pointer;
  display: block;
  color: #fff;
  font-weight: 700;
  border: none;
  transition: all 0.6s ease-in-out;
  outline: 2px solid transparent;
  font-family: "Mulish", "sans-serif";
  font-size: 18px;
  margin-top: 10px;
  width: 150px;
  border-radius: 10px;
  padding: 15px 20px;
  background: linear-gradient(
      to left,
      var(--project-color),
      var(--text-color) 50%
    )
    right;
  background-size: 200%;
  transition: 0.8s ease-out;
}
.myData:hover {
  background-position: left;
}
.hero-avatar > p {
  color: var(--project-color);
  font-family: "Lato", "sans-serif";
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  letter-spacing: 0.5px;
  font-style: italic;
}
.hero-avatar-bio {
  color: var(--text-color);
  font-weight: 900;
  font-size: 18px;
  font-style: italic;
  letter-spacing: 1.1;
  font-family: "Mulish", "sans-serif";
}
.practice {
  color: var(--text-color);
  font-style: italic;
  font-weight: 900;
  letter-spacing: 1.1;
  font-family: "Mulish", "sans-serif";
  font-size: 18px;
}
.header-emoji {
  font-style: normal;
}

