.card-size {
  position: relative;
  width: 275px;
  height: 365px;
  background-color: transparent;
}
.card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all 0.8s ease-in-out;
  background: transparent;
  cursor: pointer;
  border: 0.5px solid var(--hero-color);
  border-radius: 14px;
}
.card:hover {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.front-card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  color: var(--text-color);
  text-align: center;
  border-radius: 14px;
}
.front-card > h2 {
  padding-top: 20px;
  color: var(--text-color);
  text-align: center;
  font-size: 26px;
  font-weight: bold;
}
.front-card > img {
  display: grid;
  position: absolute;
  top: 100px;
  left: 33%;
  place-items: center;
  height: 100px;
}
.back-card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background: var(--design-color);
  color: var(--text-color);
  text-align: center;
  border-radius: 10px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.back-card > p {
  color: var(--text-color);
  padding-top: 80px;
  text-align: center;
  font-size: 18px;
  font-family: "Lato", "sans-serif";
  font-weight: 400;
  font-style: italic;
}
.back-card > h3 {
  padding-top: 20px;
  color: var(--text-color);
  text-align: center;
  font-size: 24px;
  font-weight: 900;
  font-family: "Mulish", "sans-serif";
}
@media (max-width: 992px) {
