    height: 1.5px;
    background: var(--text-color);
    top: -10px;
  }
  .bottom-section > h1 {
    font-size: 30px;
    text-align: center;
  }
  .bottom-section > h4 {
    font-size: 16px;
  }
}
@media (max-width: 320px) {
  .bottom-section > h1 {
    font-size: 24px;
    text-align: center;
  }
  .bottom-section > h4 {
    font-size: 14px;
  }
}

.footer {
  width: 100%;
  display: grid;
  margin-top: 55px;
  place-items: center;
  align-items: center;
  background-color: whitesmoke;
}
.footer > p {
  color: var(--project-color);
  font-weight: 400;
  padding-top: 10px;
  font-size: 12px;
  opacity: 1;
  font-family: "Lato", "sans-serif";
  letter-spacing: 1.1px;
  font-style: italic;
  padding: 25px 0;
}

